import React from 'react'
import imagem from '../images/familia.png'

export default function familiarIntro_atua() {
    
  return (
    
    <div className='flex items-center font-serif flex-col bg-color-white'>
        <div className='bg-color-white pt-12 pb-4 '>
            <h1 className='text-color-clicado font-bold text-4xl'>Direito familiar</h1>
        </div>
        <div className='filter grayscale hidden lg:block bg-color-white'>
            <img className='w-full' src={imagem}></img>
        </div>
        
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>O que é direito familiar?</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O Direito de Família é uma área do direito que se concentra nas relações familiares e nos aspectos legais que as envolvem. Ele abrange uma ampla gama de tópicos, incluindo casamento, divórcio, guarda de crianças, pensão alimentícia, adoção, paternidade, e muito mais.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Essa área jurídica visa regulamentar as relações familiares, proporcionando um quadro legal para lidar com questões como herança, proteção dos direitos das crianças, e resolução de conflitos familiares.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Em resumo, o Direito de Família desempenha um papel fundamental na regulamentação das relações familiares, fornecendo um conjunto de regras e princípios legais para orientar as famílias em momentos de casamento, divórcio, guarda de crianças e outras questões relacionadas à vida familiar.  Ele busca equilibrar os interesses de todos os membros da família e garantir que as decisões judiciais sejam baseadas em princípios de justiça e equidade.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Violência Doméstica e Medidas de Proteção</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A violência doméstica refere-se a qualquer forma de abuso físico, emocional, sexual ou psicológico que ocorre dentro das relações familiares ou domésticas. Isso pode ocorrer entre cônjuges, parceiros íntimos, ex-cônjuges, membros da família ou até mesmo em relações de convivência, como colegas de quarto. A violência doméstica pode assumir várias formas, incluindo agressões físicas, ameaças verbais, controle financeiro, coerção sexual e manipulação emocional. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>No Brasil, existem medidas de proteção específicas para pessoas que sofrem violência doméstica, e essas medidas são regulamentadas pela Lei Maria da Penha (Lei nº 11.340/2006), que é uma legislação voltada para a prevenção e o combate à violência contra a mulher.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Nós compreendemos profundamente a importância de oferecer apoio legal e emocional às vítimas de violência doméstica. Não apenas temos o conhecimento jurídico necessário, mas também a empatia e o compromisso de guiar e proteger aqueles que enfrentam esse desafio. Somos sua voz, sua segurança e seu caminho para uma vida livre de violência. </h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Casamento e Divórcio</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O Divórcio é uma área complexa e sensível, onde os advogados desempenham um papel crucial na negociação de acordos de divórcio. Isso inclui a divisão de bens, que pode abranger propriedades, contas bancárias, investimentos e outros ativos acumulados ao longo do casamento. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Também envolve questões de guarda de filhos e pensão alimentícia, onde os advogados ajudam a garantir que os melhores interesses das crianças sejam protegidos.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Nossa equipe de especialistas em Direito de Família está comprometida em orientar e apoiar nossos clientes em todas as etapas do processo de divórcio. Priorizamos a proteção dos direitos de nossos clientes de acordo com a lei, adaptando estratégias legais personalizadas para atender às suas necessidades específicas. Buscamos soluções que minimizem o conflito e o impacto emocional, seja por meio de negociações amigáveis, mediação ou representação litigiosa quando necessário.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Pensão Alimentícia e Partilha de Bens</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A Pensão Alimentícia refere-se à obrigação legal de um cônjuge ou ex-cônjuge fornecer apoio financeiro ao outro, especialmente quando há filhos envolvidos. Esta contribuição visa garantir que as necessidades básicas, como alimentação, moradia, educação e cuidados médicos, sejam atendidas de forma justa. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Por outro lado, a Partilha de Bens envolve a divisão dos ativos e passivos adquiridos durante o casamento ou a união estável. Isso inclui propriedades, contas bancárias, investimentos, dívidas e outros bens. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Conte conosco para orientá-lo através de todos os aspectos desse processo, assegurando que você esteja bem amparado, independentemente das complexidades financeiras envolvidas. Estamos aqui para garantir que você esteja protegido legalmente e que seus interesses e os de seus filhos sejam atendidos de maneira justa.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Guarda de crianças</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Existem diferentes tipos de guarda que podem ser aplicados, como a guarda compartilhada, guarda unilateral e guarda alternada. A guarda compartilhada é uma opção na qual ambos os pais compartilham a responsabilidade e a tomada de decisões importantes em relação à criança, enquanto na guarda unilateral, um dos pais detém a maioria das responsabilidades. A guarda alternada envolve a criança passando períodos iguais com cada um dos pais, promovendo um equilíbrio na convivência. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A decisão sobre a guarda de crianças leva em consideração diversos fatores, sempre com o melhor interesse da criança em mente. Os tribunais analisam a estabilidade emocional e financeira dos pais, o ambiente familiar, a proximidade das residências, o relacionamento da criança com cada um dos pais e a capacidade de cada um deles de atender às necessidades físicas e emocionais da criança.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Nossa empresa de advocacia é a melhor escolha para ajudá-lo a determinar o tipo de guarda mais apropriado para sua situação.  Com anos de experiência em casos de direito familiar, estamos aqui para ouvir atentamente seus desejos e preocupações para garantir o bem-estar da sua criança.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Herança</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A herança refere-se ao patrimônio, bens, propriedades e ativos que uma pessoa deixa para trás após seu falecimento. Esses ativos podem incluir propriedades imobiliárias, dinheiro, investimentos, veículos, joias, móveis e outros tipos de propriedade. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O processo de herança geralmente envolve etapas como a abertura de um inventário para listar todos os bens e ativos do falecido, o pagamento de dívidas e impostos pendentes, a identificação de herdeiros legais, a distribuição dos ativos de acordo com a vontade do falecido ou as leis de sucessão, e, em alguns casos, a resolução de disputas legais relacionadas à herança.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Independentemente da complexidade de sua situação, nossa equipe está preparada para representá-lo com dedicação e profissionalismo, assegurando que seus direitos e interesses sejam protegidos em todos os momentos. Entre em contato conosco hoje para começarmos a ajudá-lo a administrar sua herança de maneira eficaz e tranquila.</h1>
        </div>
    </div>
  )
}
